<template>
  <breadcrumb-list :items="breadcrumbs"></breadcrumb-list>

  <div class="grid grid-cols-12 gap-4 sm:gap-5 lg:gap-6">
    <div class="col-span-12 xl:col-span-8">
      <div class="p-4 card sm:p-5">
        <form
          @input="form.clearError($event.target.name)"
          @submit.prevent="submit()"
        >
          <div class="space-y-4">
            <div class="grid grid-cols-1 gap-4 sm:grid-cols-2">
              <div>
                <label for="textName">{{ $t("attributes.first_name") }}</label>
                <div class="mt-1.5">
                  <input
                    v-model="form.first_name"
                    id="textName"
                    name="first_name"
                    type="text"
                    class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                  />
                </div>

                <validation-feedback>
                  {{ form.getError("first_name") }}
                </validation-feedback>
              </div>
              <div>
                <label for="textSurname">{{ $t("attributes.surname") }}</label>
                <div class="mt-1.5">
                  <input
                    v-model="form.surname"
                    id="textSurname"
                    name="surname"
                    type="text"
                    class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                  />
                </div>

                <validation-feedback>
                  {{ form.getError("surname") }}
                </validation-feedback>
              </div>
            </div>

            <div class="grid grid-cols-1 gap-4 sm:grid-cols-2">
              <div>
                <label for="textUsername">{{
                  $t("attributes.username")
                }}</label>
                <span class="relative flex mt-1.5">
                  <input
                    v-model="form.username"
                    id="textUsername"
                    name="username"
                    type="text"
                    class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 pl-9 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                  />
                  <span
                    class="pointer-events-none absolute flex h-full w-10 items-center justify-center text-slate-400 peer-focus:text-primary dark:peer-focus:text-accent dark:text-navy-300"
                  >
                    <v-icon name="user" class="h-6 w-6"></v-icon>
                  </span>
                </span>

                <validation-feedback>
                  {{ form.getError("username") }}
                </validation-feedback>
              </div>

              <div>
                <label for="textPassword">{{
                  $t("attributes.password")
                }}</label>
                <span class="relative flex mt-1.5">
                  <input
                    v-model="form.password"
                    id="textPassword"
                    name="password"
                    type="password"
                    class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 pl-9 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                  />
                  <span
                    class="pointer-events-none absolute flex h-full w-10 items-center justify-center text-slate-400 peer-focus:text-primary dark:peer-focus:text-accent dark:text-navy-300"
                  >
                    <v-icon name="key" class="h-6 w-6"></v-icon>
                  </span>
                </span>

                <validation-feedback>
                  {{ form.getError("password") }}
                </validation-feedback>
              </div>
            </div>

            <div class="grid grid-cols-1 gap-4 sm:grid-cols-2">
              <div>
                <label for="textEmail">{{ $t("attributes.email") }}</label>
                <span class="relative flex mt-1.5">
                  <input
                    v-model="form.email"
                    id="textEmail"
                    name="email"
                    type="text"
                    class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 pl-9 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                  />
                  <span
                    class="pointer-events-none absolute flex h-full w-10 items-center justify-center text-slate-400 peer-focus:text-primary dark:peer-focus:text-accent dark:text-navy-300"
                  >
                    <v-icon name="envelope" class="h-6 w-6"></v-icon>
                  </span>
                </span>

                <validation-feedback>
                  {{ form.getError("email") }}
                </validation-feedback>
              </div>

              <div>
                <label for="textPhone">{{ $t("attributes.phone") }}</label>
                <span class="relative flex mt-1.5">
                  <v-cleave
                    v-model="form.phone"
                    :options="{ phone: true, phoneRegionCode: 'tr' }"
                    id="textPhone"
                    name="phone"
                    type="text"
                    class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 pl-9 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                  ></v-cleave>
                  <span
                    class="pointer-events-none absolute flex h-full w-10 items-center justify-center text-slate-400 peer-focus:text-primary dark:peer-focus:text-accent dark:text-navy-300"
                  >
                    <v-icon name="phone" class="h-6 w-6"></v-icon>
                  </span>
                </span>

                <validation-feedback>
                  {{ form.getError("phone") }}
                </validation-feedback>
              </div>
            </div>

            <div class="grid grid-cols-1 gap-4 sm:grid-cols-2">
              <div>
                <label for="selectHospital">{{
                  $t("attributes.hospital")
                }}</label>
                <div class="mt-1 w-full">
                  <vue-select
                    :options="hospitals"
                    label="name"
                    :reduce="(hospital) => hospital.id"
                    v-model="form.hospital_id"
                    inputId="selectHospital"
                    name="hospital_id"
                    :placeholder="$t('placeholder.select_hospital')"
                  ></vue-select>
                </div>

                <validation-feedback>
                  {{ form.getError("hospital_id") }}
                </validation-feedback>
              </div>

              <div>
                <label for="selectClinic">{{ $t("attributes.clinic") }}</label>
                <div class="mt-1 w-full">
                  <vue-select
                    :options="clinics"
                    label="name"
                    :reduce="(clinic) => clinic.id"
                    v-model="form.clinic_id"
                    inputId="selectClinic"
                    name="clinic_id"
                    :placeholder="$t('placeholder.select_clinic')"
                  ></vue-select>
                </div>

                <validation-feedback>
                  {{ form.getError("clinic_id") }}
                </validation-feedback>
              </div>
            </div>

            <div class="text-end">
              <v-button
                type="submit"
                color="primary"
                :disabled="!form.isSubmittable()"
              >
                {{ $t("labels.submit") }}
              </v-button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/core/Api";
import Form from "@/core/Form";
import Toast from "@/core/Toast";

export default {
  name: "UserForm",
  props: {
    id: {
      type: String,
      required: false,
    },
  },
  data() {
    return {
      breadcrumbs: [
        { title: this.$t("titles.users"), route: "users" },
        {
          title: this.id
            ? this.$t("models.user.edit")
            : this.$t("models.user.add"),
          active: true,
        },
      ],
      hospitals: [],
      clinics: [],
      form: new Form({
        first_name: "",
        surname: "",
        username: "",
        password: "",
        email: "",
        phone: "",
        hospital_id: null,
        clinic_id: null,
      }),
    };
  },
  async created() {
    await api.get("/hospitals/all").then(({ data }) => {
      this.hospitals = data;
    });

    await api.get("/clinics/all").then(({ data }) => {
      this.clinics = data;
    });

    if (this.id) {
      await api.get(`/user/${this.id}`).then(({ data }) => {
        this.form.updateData(data);
      });
    }
  },
  methods: {
    submit() {
      if (this.form.isUpdate()) {
        this.form
          .post("/user/update", this.form.getId())
          .then(() => {
            Toast.fire(this.$t("response.success.update"), "success");
          })
          .catch(() => {
            Toast.fire(this.form.getErrorMessage(), "error");
          });
      } else {
        this.form
          .post("/user/store")
          .then(() => {
            Toast.fire(this.$t("response.success.store"), "success");
          })
          .catch(() => {
            Toast.fire(this.form.getErrorMessage(), "error");
          });
      }
    },
  },
};
</script>
